header {
    min-height: 80px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    z-index: 1;
    background-color: lightgrey;
    opacity: 0.8;
    position: fixed;
    top: 0;

}

header .logo {
    letter-spacing: 3px;
}

header .logo a{
    text-decoration: none;
    color: black;
}

header nav {
    display: flex;
}

nav ul li {
    list-style: none;
    display: inline-block;

}

nav ul li a {
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 15px;
}

nav ul li a:hover {
    color: lightseagreen
}

nav .nav-cart {
    position: relative;
}

nav .nav-cart span {
    position: absolute;
    top: -12px;
    right: -10px;
    background: crimson;
    font-size: 10px;
    color: white;
    padding: 3px 5px;
    border-radius: 50%;
    z-index: -1;
}

header .menu,
header .close {
    cursor: pointer;
    display: none;
}

@media (max-width: 520px) {
    header ul {
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        background: white;
        z-index: 99;
        opacity: 0.95;
        transition: 0.5s linear;
    }

    header .menu,
    header .close {
        display: block;
    }

    header .close {
        position: absolute;
        top: 10px;
        
    }

    header ul.toggle {
        top: 0;
    }
}